@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
.pending-head{
    padding: 2vh 0;
    text-align: center;
    font: 6vh "Ubuntu", Arial;
    font-weight: bold;
    color: rgb(92, 0, 128);
}
.pending-container{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.pending-item{
    width: 70%;
    padding: 2vh 0 2vh 1vw;
    margin: 1vh 15%;
    background-color: white;
    border: 1px solid grey;
    border-radius: 15px;
}
.pending-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 2vw;
    padding-bottom: 1vh;
    border-bottom: 1px solid grey;
}
.pending-ordid{
    font: 3vh "Nunito", Arial;
}
.pending-stat{
    font: 4vh "Nunito", Arial;
    font-weight: bold;
    color: white;
    padding: 3px 10px;
    border-radius: 10px;
}
.pending-custmail{
    text-align: right;
    font: 2.4vh "Ubuntu", Arial;
    color: grey;
}
.pending-top-wrap{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2vh 0;
}
.pending-left-wrap{
    width: 20%;
    display: flex;
    justify-content: center;
}
.pending-left-wrap img{
    height: 17vh;
}
.pending-mid-wrap{
    padding: 1vh 0;
    width: 40%;
    height: 15vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-right: 1px solid grey;
}
.pending-pname{
    text-align: center;
    font: 3.5vh "Nunito", Arial;
    font-weight: bold;
    color: purple;
}
.pending-qty{
    text-align: center;
    font: 3vh "Nunito", Arial;
    color: grey;
}
.pending-right-wrap{
    width: 40%;
    height: 15vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 1.5vw;
}
.deliv-add{
    font: 2.9vh "Nunito", Arial;
    line-height: 3.4vh;
}
.update-order-btn{
    margin-top: 2vh;
    padding: 2px 0;
    width: 100%;
    font: 2.5vh "Poppins", Arial;
    color: navy;
    font-weight: bold;
    background-color: rgb(234, 243, 255);
    border: 2px solid rgb(0, 44, 187);
    transition: all 0.2s ease-out;
}
.update-order-btn:hover{
    cursor: pointer;
    background-color: rgba(0, 44, 187, 0.897);
    color: white;
}
